// Original code from: https://github.com/N8python/n8ao
// ported to TypeScript

import { Matrix4, Uniform, Vector2, Vector3 } from "three";

const EffectCompositer = {
  uniforms: {
    sceneDiffuse: new Uniform(null),
    sceneDepth: new Uniform(null),
    tDiffuse: new Uniform(null),
    projMat: new Uniform(new Matrix4()),
    viewMat: new Uniform(new Matrix4()),
    projectionMatrixInv: new Uniform(new Matrix4()),
    viewMatrixInv: new Uniform(new Matrix4()),
    cameraPos: new Uniform(new Vector3()),
    resolution: new Uniform(new Vector2()),
    color: new Uniform(new Vector3()),
    blueNoise: new Uniform(null),
    downsampledDepth: new Uniform(null),
    time: new Uniform(0.0),
    intensity: new Uniform(10.0),
    renderMode: new Uniform(0.0),
    gammaCorrection: new Uniform(false),
    logDepth: new Uniform(false),
    ortho: new Uniform(false),
    near: new Uniform(0.1),
    far: new Uniform(1000.0),
    screenSpaceRadius: new Uniform(false),
    radius: new Uniform(0.0),
    distanceFalloff: new Uniform(1.0),
    fog: new Uniform(false),
    fogExp: new Uniform(false),
    fogDensity: new Uniform(0.0),
    fogNear: new Uniform(Infinity),
    fogFar: new Uniform(Infinity),
    colorMultiply: new Uniform(true),
  },

  depthWrite: false,

  depthTest: false,

  vertexShader: /* glsl */ `
    varying vec2 vUv;
    void main(void) {
      vUv = uv;
      gl_Position = vec4(position, 1);
    }`,

  fragmentShader: /* glsl */ `
    uniform sampler2D sceneDiffuse;
    uniform highp sampler2D sceneDepth;
    uniform highp sampler2D downsampledDepth;
    uniform sampler2D tDiffuse;
    uniform sampler2D blueNoise;
    uniform vec2 resolution;
    uniform vec3 color;
    uniform mat4 projectionMatrixInv;
    uniform mat4 viewMatrixInv;
    uniform float intensity;
    uniform float renderMode;
    uniform float near;
    uniform float far;
    uniform bool gammaCorrection;
    uniform bool logDepth;
    uniform bool ortho;
    uniform bool screenSpaceRadius;
    uniform bool fog;
    uniform bool fogExp;
    uniform bool colorMultiply;
    uniform float fogDensity;
    uniform float fogNear;
    uniform float fogFar;
    uniform float radius;
    uniform float distanceFalloff;
    uniform vec3 cameraPos;
    varying vec2 vUv;

    highp float linearize_depth(highp float d, highp float zNear,highp float zFar) {
      return (zFar * zNear) / (zFar - d * (zFar - zNear));
    }
   
    highp float linearize_depth_ortho(highp float d, highp float nearZ, highp float farZ) {
      return nearZ + (farZ - nearZ) * d;
    }

    highp float linearize_depth_log(highp float d, highp float nearZ,highp float farZ) {
      float depth = pow(2.0, d * log2(farZ + 1.0)) - 1.0;
      float a = farZ / (farZ - nearZ);
      float b = farZ * nearZ / (nearZ - farZ);
      float linDepth = a + b / depth;
      return (
        ortho
          ? linearize_depth_ortho(linDepth, nearZ, farZ)
          : linearize_depth(linDepth, nearZ, farZ)
      );
    }

    vec3 getWorldPosLog(vec3 posS) {
      vec2 uv = posS.xy;
      float z = posS.z;
      float nearZ =near;
      float farZ = far;
      float depth = pow(2.0, z * log2(farZ + 1.0)) - 1.0;
      float a = farZ / (farZ - nearZ);
      float b = farZ * nearZ / (nearZ - farZ);
      float linDepth = a + b / depth;
      vec4 clipVec = vec4(uv, linDepth, 1.0) * 2.0 - 1.0;
      vec4 wpos = projectionMatrixInv * clipVec;
      return wpos.xyz / wpos.w;
    }

    vec3 getWorldPos(float depth, vec2 coord) {
      #ifdef LOGDEPTH
        return getWorldPosLog(vec3(coord, depth));
      #endif

      float z = depth * 2.0 - 1.0;
      vec4 clipSpacePosition = vec4(coord * 2.0 - 1.0, z, 1.0);
      vec4 viewSpacePosition = projectionMatrixInv * clipSpacePosition;

      if (abs(viewSpacePosition.w) < 1e-6) {
        discard;
      }

      viewSpacePosition /= viewSpacePosition.w;
      return (viewMatrixInv * viewSpacePosition).xyz;
    }

    vec4 LinearTosRGB(vec4 linearRGB) {
      bvec4 cutoff = lessThan(linearRGB, vec4(0.0031308));
      vec4 higher = vec4(1.055)*pow(linearRGB, vec4(1.0/2.4)) - vec4(0.055);
      vec4 lower = linearRGB * vec4(12.92);
      return mix(higher, lower, cutoff);
    }


    vec3 computeNormal(vec3 worldPos, vec2 vUv) {
      ivec2 p = ivec2(vUv * resolution);
      float c0 = texelFetch(sceneDepth, p, 0).x;
      float l2 = texelFetch(sceneDepth, p - ivec2(2, 0), 0).x;
      float l1 = texelFetch(sceneDepth, p - ivec2(1, 0), 0).x;
      float r1 = texelFetch(sceneDepth, p + ivec2(1, 0), 0).x;
      float r2 = texelFetch(sceneDepth, p + ivec2(2, 0), 0).x;
      float b2 = texelFetch(sceneDepth, p - ivec2(0, 2), 0).x;
      float b1 = texelFetch(sceneDepth, p - ivec2(0, 1), 0).x;
      float t1 = texelFetch(sceneDepth, p + ivec2(0, 1), 0).x;
      float t2 = texelFetch(sceneDepth, p + ivec2(0, 2), 0).x;
      float dl = abs((2.0 * l1 - l2) - c0);
      float dr = abs((2.0 * r1 - r2) - c0);
      float db = abs((2.0 * b1 - b2) - c0);
      float dt = abs((2.0 * t1 - t2) - c0);
      vec3 ce = getWorldPos(c0, vUv).xyz;
      vec3 dpdx = (
        (dl < dr)
          ? ce - getWorldPos(l1, (vUv - vec2(1.0 / resolution.x, 0.0))).xyz
          : -ce + getWorldPos(r1, (vUv + vec2(1.0 / resolution.x, 0.0))).xyz
      );
      vec3 dpdy = (
        (db < dt)
          ? ce - getWorldPos(b1, (vUv - vec2(0.0, 1.0 / resolution.y))).xyz
          : -ce + getWorldPos(t1, (vUv + vec2(0.0, 1.0 / resolution.y))).xyz
      );
      return normalize(cross(dpdx, dpdy));
    }

    #include <common>
    #include <dithering_pars_fragment>

    void main(void) {
      vec4 sceneTexel = texture2D(sceneDiffuse, vUv);
      float depth = texture2D(sceneDepth, vUv).x;
     
      #ifdef HALFRES 
        vec4 texel;
        if (depth == 1.0) {
          texel = vec4(0.0, 0.0, 0.0, 1.0);
        } else {
          vec3 worldPos = getWorldPos(depth, vUv);
          vec3 normal = computeNormal(getWorldPos(depth, vUv), vUv);

          float totalWeight = 0.0;
          float radiusToUse = (
            screenSpaceRadius
              ? distance(worldPos, getWorldPos(depth, vUv + vec2(radius, 0.0) / resolution))
              : radius
          );
          float distanceFalloffToUse = (
            screenSpaceRadius
              ? radiusToUse * distanceFalloff
              : distanceFalloff
          );
          for(float x = -1.0; x <= 1.0; x++) {
            for(float y = -1.0; y <= 1.0; y++) {
              vec2 offset = vec2(x, y);
              ivec2 p = ivec2((vUv * resolution * 0.5) + offset);
              vec2 pUv = vec2(p) / (resolution * 0.5);
              float sampleDepth = texelFetch(downsampledDepth,p, 0).x;
              vec4 sampleInfo = texelFetch(tDiffuse, p, 0);
              vec3 normalSample = sampleInfo.xyz * 2.0 - 1.0;
              vec3 worldPosSample = getWorldPos(sampleDepth, pUv);
              float tangentPlaneDist = abs(dot(worldPos - worldPosSample, normal));
              float rangeCheck = exp(
                -1.0 * tangentPlaneDist * (1.0 / distanceFalloffToUse)) * max(dot(normal, normalSample),
                0.0
              );
              float weight = rangeCheck;
              totalWeight += weight;
              texel += sampleInfo * weight;
            }
          }
          if (totalWeight == 0.0) {
            texel = texture2D(tDiffuse, vUv);
          } else {
            texel /= totalWeight;
          }
        }
      #else
        vec4 texel = texture2D(tDiffuse, vUv);
      #endif

      #ifdef LOGDEPTH
        texel.a = clamp(texel.a, 0.0, 1.0);
        if (texel.a == 0.0) {
          texel.a = 1.0;
        }
      #endif

      float finalAo = pow(texel.a, intensity);
      float fogFactor;
      float fogDepth = distance(cameraPos, getWorldPos(depth, vUv));

      if (fog) {
        if (fogExp) {
          fogFactor = 1.0 - exp(-fogDensity * fogDensity * fogDepth * fogDepth);
        } else {
          fogFactor = smoothstep(fogNear, fogFar, fogDepth);
        }
      }

      finalAo = mix(finalAo, 1.0, fogFactor);
      vec3 aoApplied = color * mix(vec3(1.0), sceneTexel.rgb, float(colorMultiply));
      if (renderMode == 0.0) {
        gl_FragColor = vec4( mix(sceneTexel.rgb, aoApplied, 1.0 - finalAo), sceneTexel.a);
      } else if (renderMode == 1.0) {
        gl_FragColor = vec4( mix(vec3(1.0), aoApplied, 1.0 - finalAo), sceneTexel.a);
      } else if (renderMode == 2.0) {
        gl_FragColor = vec4( sceneTexel.rgb, sceneTexel.a);
      } else if (renderMode == 3.0) {
        if (vUv.x < 0.5) {
          gl_FragColor = vec4( sceneTexel.rgb, sceneTexel.a);
        } else if (abs(vUv.x - 0.5) < 1.0 / resolution.x) {
          gl_FragColor = vec4(1.0);
        } else {
          gl_FragColor = vec4(mix(sceneTexel.rgb, aoApplied, 1.0 - finalAo), sceneTexel.a);
        }
      } else if (renderMode == 4.0) {
        if (vUv.x < 0.5) {
          gl_FragColor = vec4( sceneTexel.rgb, sceneTexel.a);
        } else if (abs(vUv.x - 0.5) < 1.0 / resolution.x) {
          gl_FragColor = vec4(1.0);
        } else {
          gl_FragColor = vec4(mix(vec3(1.0), aoApplied, 1.0 - finalAo), sceneTexel.a);
        }
      }

      #include <dithering_fragment>

      if (gammaCorrection) {
        gl_FragColor = LinearTosRGB(gl_FragColor);
      }
    }
    `,
};
export { EffectCompositer };
